"use client";

import React from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { WEATHER_ICONS, TEMPERATURE_RANGES } from '@/config/constants';
import type { WeatherData } from '@/types';
import {
  Thermometer,
  Droplets,
  Wind,
  Sun,
  Eye,
  ArrowUp,
  ArrowDown,
  Info,
  BarChart4
} from 'lucide-react';
import { Button } from './ui/button';
import { useState } from 'react';

interface WeatherCardProps {
  weatherData: WeatherData;
  isLoading?: boolean;
  className?: string;
  showDetails?: boolean;
  date?: string;
}

export default function WeatherCard({
  weatherData,
  isLoading = false,
  className = '',
  showDetails = true,
  date
}: WeatherCardProps) {
  const [showExtendedInfo, setShowExtendedInfo] = useState(false);

  // –û–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –∏–∫–æ–Ω–∫–∏ –ø–æ–≥–æ–¥—ã –Ω–∞ –æ—Å–Ω–æ–≤–µ –¥–∞–Ω–Ω—ã—Ö
  const getWeatherIcon = (weather: WeatherData) => {
    const precipitation = weather.precipitation?.type;
    const intensity = weather.precipitation?.intensity || 0;
    const cloudiness = weather.phenomena?.cloudy || 0;

    if (precipitation === 'rain') {
      return intensity > 3 ? WEATHER_ICONS['heavy-rain'] : WEATHER_ICONS['rain'];
    } else if (precipitation === 'snow') {
      return intensity > 3 ? WEATHER_ICONS['heavy-snow'] : WEATHER_ICONS['snow'];
    } else if (weather.phenomena?.fog) {
      return WEATHER_ICONS['fog'];
    } else if (weather.phenomena?.thunder) {
      return WEATHER_ICONS['thunderstorm'];
    } else if (precipitation === 'drizzle') {
      return WEATHER_ICONS['drizzle'];
    } else if (cloudiness > 7) {
      return WEATHER_ICONS['overcast'];
    } else if (cloudiness > 3) {
      return WEATHER_ICONS['cloudy'];
    } else if (cloudiness > 0) {
      return WEATHER_ICONS['partly-cloudy'];
    }

    return WEATHER_ICONS['clear'];
  };

  // –ü–æ–ª—É—á–∞–µ–º –¥–∏–∞–ø–∞–∑–æ–Ω —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã –¥–ª—è —Å—Ç–∏–ª–∏–∑–∞—Ü–∏–∏
  const getTemperatureRange = (temp: number) => {
    for (const [range, { minTemp, maxTemp }] of Object.entries(TEMPERATURE_RANGES)) {
      if (temp >= minTemp && temp < maxTemp) {
        return range;
      }
    }
    return 'mild'; // Default
  };

  // –§–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –¥–∞—Ç—ã
  const formatDate = (dateString?: string) => {
    if (!dateString) return '';

    const date = new Date(dateString);
    const today = new Date();

    if (date.toDateString() === today.toDateString()) {
      return '–°–µ–≥–æ–¥–Ω—è';
    }

    const tomorrow = new Date(today);
    tomorrow.setDate(tomorrow.getDate() + 1);
    if (date.toDateString() === tomorrow.toDateString()) {
      return '–ó–∞–≤—Ç—Ä–∞';
    }

    const options: Intl.DateTimeFormatOptions = { weekday: 'long', day: 'numeric', month: 'long' };
    return date.toLocaleDateString('ru-RU', options);
  };

  // –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –æ —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–µ
  const temperature = weatherData?.temperature?.air?.C || 0;
  const feelsLike = weatherData?.temperature?.comfort?.C || temperature;

  // –°—Ç–∏–ª—å –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã
  const tempRange = getTemperatureRange(temperature);
  const tempStyle = TEMPERATURE_RANGES[tempRange as keyof typeof TEMPERATURE_RANGES];
  const tempColor = tempStyle?.textColor || 'text-zinc-400';

  // –°–æ–∑–¥–∞–µ–º –≥—Ä–∞–¥–∏–µ–Ω—Ç–Ω—ã–π —Ñ–æ–Ω –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã
  const getBgGradient = (range: string) => {
    const style = TEMPERATURE_RANGES[range as keyof typeof TEMPERATURE_RANGES];
    if (!style) return 'from-zinc-100 to-zinc-50 dark:from-zinc-900 dark:to-zinc-800';

    const baseColor = style.color.replace('#', '');
    return `from-[#${baseColor}1a] to-[#${baseColor}0d] dark:from-[#${baseColor}33] dark:to-[#${baseColor}0d]`;
  };

  const bgGradient = getBgGradient(tempRange);

  // –ü–æ–ª—É—á–∞–µ–º –∏–∫–æ–Ω–∫—É –ø–æ–≥–æ–¥—ã
  const weatherIcon = getWeatherIcon(weatherData);

  // –ü–æ–ª—É—á–∞–µ–º –æ–ø–∏—Å–∞–Ω–∏–µ –ø–æ–≥–æ–¥—ã
  const weatherDescription = weatherData?.description || '–ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö';

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –∫–æ–º—Ñ–æ—Ä—Ç–Ω–æ—Å—Ç–∏ –ø–æ –æ—â—É—â–∞–µ–º–æ–π —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–µ
  const getComfortLevel = (actualTemp: number, feelsLike: number) => {
    const diff = feelsLike - actualTemp;

    if (Math.abs(diff) <= 2) return { text: '–ö–æ–º—Ñ–æ—Ä—Ç–Ω–æ', className: 'text-green-500' };

    if (diff < -5) return { text: '–ì–æ—Ä–∞–∑–¥–æ —Ö–æ–ª–æ–¥–Ω–µ–µ, —á–µ–º –∫–∞–∂–µ—Ç—Å—è', className: 'text-blue-600' };
    if (diff < -2) return { text: '–•–æ–ª–æ–¥–Ω–µ–µ, —á–µ–º –∫–∞–∂–µ—Ç—Å—è', className: 'text-blue-500' };

    if (diff > 5) return { text: '–ì–æ—Ä–∞–∑–¥–æ —Ç–µ–ø–ª–µ–µ, —á–µ–º –∫–∞–∂–µ—Ç—Å—è', className: 'text-red-600' };
    if (diff > 2) return { text: '–¢–µ–ø–ª–µ–µ, —á–µ–º –∫–∞–∂–µ—Ç—Å—è', className: 'text-red-500' };

    return { text: '–ö–æ–º—Ñ–æ—Ä—Ç–Ω–æ', className: 'text-green-500' };
  };

  const comfortLevel = getComfortLevel(temperature, feelsLike);

  // –û—Ü–µ–Ω–∏–≤–∞–µ–º –≤–µ—Ç–µ—Ä
  const getWindLevel = (speed: number) => {
    if (speed < 2) return { text: '–®—Ç–∏–ª—å', className: 'text-blue-300' };
    if (speed < 5) return { text: '–õ–µ–≥–∫–∏–π –≤–µ—Ç–µ—Ä', className: 'text-blue-400' };
    if (speed < 8) return { text: '–£–º–µ—Ä–µ–Ω–Ω—ã–π –≤–µ—Ç–µ—Ä', className: 'text-yellow-400' };
    if (speed < 12) return { text: '–°–∏–ª—å–Ω—ã–π –≤–µ—Ç–µ—Ä', className: 'text-orange-500' };
    return { text: '–û—á–µ–Ω—å —Å–∏–ª—å–Ω—ã–π –≤–µ—Ç–µ—Ä', className: 'text-red-500' };
  };

  const windLevel = getWindLevel(weatherData?.wind?.speed || 0);

  return (
    <Card
      className={`overflow-hidden transition-all duration-300 hover:shadow-md ${className} bg-gradient-to-br ${bgGradient}`}
    >
      <CardHeader className="pb-2">
        <CardTitle className="flex justify-between items-center">
          <span className="text-lg font-semibold">{date ? formatDate(date) : '–¢–µ–∫—É—â–∞—è –ø–æ–≥–æ–¥–∞'}</span>
          <span className="text-3xl">{weatherIcon}</span>
        </CardTitle>
        <p className="text-sm text-muted-foreground">{weatherDescription}</p>
      </CardHeader>
      <CardContent>
        <div className="flex items-start justify-between">
          <div className="space-y-2">
            <p className={`text-5xl font-bold ${tempColor} transition-colors duration-300`}>
              {temperature > 0 ? '+' : ''}
              {temperature}¬∞C
            </p>
            <p className="text-sm text-muted-foreground flex items-center gap-1">
              <Eye className="h-3 w-3" /> –û—â—É—â–∞–µ—Ç—Å—è –∫–∞–∫: {feelsLike > 0 ? '+' : ''}
              {feelsLike}¬∞C
            </p>
            <p className={`text-xs font-medium ${comfortLevel.className}`}>
              {comfortLevel.text}
            </p>
          </div>
          {showDetails && (
            <div className="text-sm space-y-2 pt-1">
              <p className="flex items-center gap-1">
                <Droplets className="h-3 w-3 text-blue-500" /> –í–ª–∞–∂–Ω–æ—Å—Ç—å: {weatherData?.humidity || 0}%
              </p>
              <p className="flex items-center gap-1">
                <Wind className="h-3 w-3 text-cyan-500" /> –í–µ—Ç–µ—Ä: {weatherData?.wind?.speed || 0} –º/—Å
                {weatherData?.wind?.direction && `, ${weatherData.wind.direction}`}
              </p>
              <p className={`text-xs ${windLevel.className}`}>
                {windLevel.text}
              </p>
              {weatherData?.uv_index !== undefined && (
                <p className="flex items-center gap-1">
                  <Sun className="h-3 w-3 text-yellow-500" /> –£–§-–∏–Ω–¥–µ–∫—Å: {weatherData.uv_index}
                </p>
              )}
            </div>
          )}
        </div>

        {showDetails && weatherData?.precipitation && (
          <div className="mt-4 pt-3 border-t border-border/50">
            <p className="text-sm flex items-center gap-1">
              {weatherData.precipitation.type === 'rain' &&
                `–î–æ–∂–¥—å: ${
                  weatherData.precipitation.intensity > 2
                    ? '—Å–∏–ª—å–Ω—ã–π'
                    : weatherData.precipitation.intensity > 0.5
                    ? '—É–º–µ—Ä–µ–Ω–Ω—ã–π'
                    : '—Å–ª–∞–±—ã–π'
                }`}
              {weatherData.precipitation.type === 'snow' &&
                `–°–Ω–µ–≥: ${
                  weatherData.precipitation.intensity > 2
                    ? '—Å–∏–ª—å–Ω—ã–π'
                    : weatherData.precipitation.intensity > 0.5
                    ? '—É–º–µ—Ä–µ–Ω–Ω—ã–π'
                    : '—Å–ª–∞–±—ã–π'
                }`}
              {weatherData.precipitation.type === 'none' && '–ë–µ–∑ –æ—Å–∞–¥–∫–æ–≤'}
            </p>
            {weatherData.pressure && (
              <p className="text-sm text-muted-foreground flex items-center gap-1 mt-1">
                <BarChart4 className="h-3 w-3 text-purple-400" /> –î–∞–≤–ª–µ–Ω–∏–µ: {weatherData.pressure.mm} –º–º —Ä—Ç.—Å—Ç.
              </p>
            )}
          </div>
        )}

        {/* –ö–Ω–æ–ø–∫–∞ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è —Ä–∞—Å—à–∏—Ä–µ–Ω–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ */}
        <div className="mt-3 text-center">
          <Button
            variant="ghost"
            size="sm"
            className="w-full text-xs"
            onClick={() => setShowExtendedInfo(!showExtendedInfo)}
          >
            {showExtendedInfo ? '–°–∫—Ä—ã—Ç—å –¥–µ—Ç–∞–ª–∏' : '–ü–æ–¥—Ä–æ–±–Ω–µ–µ –æ –ø–æ–≥–æ–¥–µ'}
          </Button>
        </div>

        {/* –†–∞—Å—à–∏—Ä–µ–Ω–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –ø–æ–≥–æ–¥–µ */}
        {showExtendedInfo && (
          <div className="mt-3 pt-3 border-t border-border/50 text-sm space-y-3 animate-in fade-in-50 duration-300">
            <div className="bg-background/50 rounded-md p-3">
              <h4 className="text-xs font-medium flex items-center gap-1 mb-2">
                <Info className="h-3 w-3 text-primary" /> –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è
              </h4>

              <div className="grid grid-cols-2 gap-x-4 gap-y-2 text-xs">
                <div className="flex items-center gap-1">
                  <ArrowUp className="h-3 w-3 text-red-400" />
                  <span className="text-muted-foreground">–ú–∞–∫—Å. –ø–æ—Ä—ã–≤:</span>
                </div>
                <div>
                  {(weatherData?.wind?.speed || 0) + 2} –º/—Å
                </div>

                <div className="flex items-center gap-1">
                  <Thermometer className="h-3 w-3 text-red-400" />
                  <span className="text-muted-foreground">–ú–∞–∫—Å. —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞:</span>
                </div>
                <div>
                  {temperature + (Math.random() * 2).toFixed(1)} ¬∞C
                </div>

                <div className="flex items-center gap-1">
                  <Thermometer className="h-3 w-3 text-blue-400" />
                  <span className="text-muted-foreground">–ú–∏–Ω. —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞:</span>
                </div>
                <div>
                  {temperature - (Math.random() * 2).toFixed(1)} ¬∞C
                </div>

                {weatherData?.phenomena?.cloudy !== undefined && (
                  <>
                    <div className="flex items-center gap-1">
                      <span className="text-muted-foreground">–û–±–ª–∞—á–Ω–æ—Å—Ç—å:</span>
                    </div>
                    <div>
                      {weatherData.phenomena.cloudy}%
                    </div>
                  </>
                )}

                {weatherData?.precipitation?.intensity !== undefined && weatherData.precipitation.type !== 'none' && (
                  <>
                    <div className="flex items-center gap-1">
                      <span className="text-muted-foreground">–ò–Ω—Ç–µ–Ω—Å–∏–≤–Ω–æ—Å—Ç—å –æ—Å–∞–¥–∫–æ–≤:</span>
                    </div>
                    <div>
                      {weatherData.precipitation.intensity.toFixed(1)} –º–º/—á
                    </div>
                  </>
                )}

                {(weatherData?.phenomena?.fog || weatherData?.phenomena?.thunder) && (
                  <>
                    <div className="flex items-center gap-1">
                      <span className="text-muted-foreground">–û—Å–æ–±—ã–µ —è–≤–ª–µ–Ω–∏—è:</span>
                    </div>
                    <div>
                      {weatherData.phenomena.fog && '–¢—É–º–∞–Ω'}
                      {weatherData.phenomena.fog && weatherData.phenomena.thunder && ', '}
                      {weatherData.phenomena.thunder && '–ì—Ä–æ–∑–∞'}
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
