"use client";

import { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import Navigation from "@/components/navigation";
import Footer from "@/components/footer";
import WeatherCard from "@/components/weather-card";
import ClothingRecommendations from "@/components/clothing-recommendations";
import ActivitySelector from "@/components/activity-selector";
import type { ActivityType } from "@/types";
import { Card, CardContent } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Separator } from "@/components/ui/separator";
import { Skeleton } from "@/components/ui/skeleton";
import { ArrowLeft, Calendar, RefreshCw, MapPin } from "lucide-react";
import Link from "next/link";

export default function WeatherPage() {
  const { cityId } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [cityName, setCityName] = useState("");
  const [weatherData, setWeatherData] = useState(null);
  const [forecastData, setForecastData] = useState<any[]>([]);
  const [recommendations, setRecommendations] = useState<any | null>(null);
  const [forecastRecommendations, setForecastRecommendations] = useState<any[]>([]);
  const [activityType, setActivityType] = useState<ActivityType>("walk");
  const [refreshing, setRefreshing] = useState(false);
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);

  // –ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–≥–æ–¥–µ –∏ —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–π
  useEffect(() => {
    const fetchData = async () => {
      if (!cityId) return;

      setIsLoading(true);
      setError(null);

      try {
        // –ü–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–≥–æ–¥–µ –∏ —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏ —á–µ—Ä–µ–∑ API
        const weatherResponse = await fetch(`/api/weather/current/${cityId}`);
        if (!weatherResponse.ok) {
          throw new Error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–≥–æ–¥–µ");
        }
        const currentWeather = await weatherResponse.json();

        // –ü–æ–ª—É—á–∞–µ–º –ø—Ä–æ–≥–Ω–æ–∑ –ø–æ–≥–æ–¥—ã
        const forecastResponse = await fetch(`/api/weather/forecast/${cityId}?days=3`);
        if (!forecastResponse.ok) {
          throw new Error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –ø—Ä–æ–≥–Ω–æ–∑–∞ –ø–æ–≥–æ–¥—ã");
        }
        const forecast = await forecastResponse.json();

        // –ü–æ–ª—É—á–∞–µ–º —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏ –ø–æ –æ–¥–µ–∂–¥–µ
        const recommendationsResponse = await fetch(
          `/api/recommendations/${cityId}?activity_type=${activityType}`
        );
        if (!recommendationsResponse.ok) {
          throw new Error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–π");
        }
        const recommendationsData = await recommendationsResponse.json();

        // –ó–∞–≥—Ä—É–∂–∞–µ–º —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏ –¥–ª—è –ø—Ä–æ–≥–Ω–æ–∑–∞ –Ω–∞ –±—É–¥—É—â–∏–µ –¥–Ω–∏
        const forecastRecsPromises = forecast.map((dayData: any) =>
          fetch(
            `/api/recommendations/forecast/${cityId}?date=${dayData.date}&activity_type=${activityType}`
          )
            .then((response) => {
              if (!response.ok) return null;
              return response.json();
            })
            .catch(() => null)
        );

        const forecastRecommendationsData = await Promise.all(forecastRecsPromises);

        // –ü–æ–ª—É—á–∞–µ–º –Ω–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Ä–æ–¥–∞ –∏–∑ –ø–æ–ø—É–ª—è—Ä–Ω—ã—Ö –≥–æ—Ä–æ–¥–æ–≤ –∏–ª–∏ –∏—Å–ø–æ–ª—å–∑—É–µ–º ID
        const { POPULAR_CITIES } = await import("@/config/constants");
        const city = POPULAR_CITIES.find((c) => c.id.toString() === (cityId as string));
        setCityName(city ? city.name : `–ì–æ—Ä–æ–¥ —Å ID ${cityId}`);

        setWeatherData(currentWeather);
        setForecastData(forecast);
        setRecommendations(recommendationsData.recommendations);
        setForecastRecommendations(
          forecastRecommendationsData
            .filter(Boolean)
            .map((item: any) => item?.recommendations || null)
        );
        setLastUpdated(new Date());
      } catch (err) {
        console.error("–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –¥–∞–Ω–Ω—ã—Ö:", err);
        setError("–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–≥–æ–¥–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.");
      } finally {
        setIsLoading(false);
        setRefreshing(false);
      }
    };

    fetchData();
  }, [cityId, activityType]);

  const handleActivityChange = (value: ActivityType) => {
    setActivityType(value);
  };

  const handleRefresh = () => {
    setRefreshing(true);
    // –ü–æ–≤—Ç–æ—Ä–Ω–æ–µ –ø–æ–ª—É—á–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –æ –ø–æ–≥–æ–¥–µ
    fetch(`/api/weather/current/${cityId}`)
      .then((response) => {
        if (!response.ok) throw new Error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö");
        return response.json();
      })
      .then((currentWeather) => {
        setWeatherData(currentWeather);
        setLastUpdated(new Date());
      })
      .catch((err) => {
        console.error("–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö:", err);
      })
      .finally(() => {
        setRefreshing(false);
      });
  };

  return (
    <div className="flex min-h-screen flex-col bg-gradient-to-b from-background to-muted/30">
      <Navigation />
      <main className="flex-1 py-8">
        <div className="container px-4 md:px-6">
          <div className="mb-6 flex items-center justify-between">
            <Link
              href="/"
              className="inline-flex items-center gap-1 text-sm text-muted-foreground hover:text-foreground"
            >
              <ArrowLeft className="h-4 w-4" />
              <span>–ù–∞–∑–∞–¥</span>
            </Link>
            {!isLoading && lastUpdated && (
              <button
                onClick={handleRefresh}
                className="inline-flex items-center gap-1 text-sm text-muted-foreground hover:text-foreground transition-all"
                disabled={refreshing}
                aria-label="–û–±–Ω–æ–≤–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–≥–æ–¥–µ"
              >
                <RefreshCw className={`h-4 w-4 ${refreshing ? "animate-spin" : ""}`} />
                <span>–û–±–Ω–æ–≤–∏—Ç—å</span>
              </button>
            )}
          </div>

          {isLoading ? (
            <div className="space-y-4">
              <Skeleton className="h-12 w-3/4" />
              <div className="grid gap-6 md:grid-cols-2">
                <Skeleton className="h-64" />
                <Skeleton className="h-64" />
              </div>
              <Skeleton className="h-12 w-1/2 mt-6" />
              <div className="grid gap-4 md:grid-cols-3 mt-4">
                <Skeleton className="h-48" />
                <Skeleton className="h-48" />
                <Skeleton className="h-48" />
              </div>
            </div>
          ) : error ? (
            <Card>
              <CardContent className="p-6">
                <div className="text-center">
                  <h2 className="text-2xl font-bold text-red-600">–û—à–∏–±–∫–∞</h2>
                  <p className="mt-2">{error}</p>
                </div>
              </CardContent>
            </Card>
          ) : (
            <>
              <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-2 mb-6">
                <h1 className="text-3xl font-bold flex items-center gap-2">
                  <MapPin className="h-6 w-6 text-primary" />
                  <span>{cityName}</span>
                </h1>
                {lastUpdated && (
                  <div className="text-sm text-muted-foreground">
                    –ü–æ—Å–ª–µ–¥–Ω–µ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ: {lastUpdated.toLocaleTimeString()}
                  </div>
                )}
              </div>

              <div className="mb-8">
                <ActivitySelector defaultValue={activityType} onChange={handleActivityChange} />
              </div>

              <div className="grid gap-6 md:grid-cols-2">
                {weatherData && <WeatherCard weatherData={weatherData} showDetails={true} />}

                {recommendations && (
                  <ClothingRecommendations recommendations={recommendations} />
                )}
              </div>

              <Separator className="my-8" />

              <div>
                <h2 className="text-2xl font-bold mb-4 flex items-center gap-2">
                  <Calendar className="h-5 w-5" />
                  –ü—Ä–æ–≥–Ω–æ–∑ –Ω–∞ –±–ª–∏–∂–∞–π—à–∏–µ –¥–Ω–∏
                </h2>

                <Tabs defaultValue="weather" className="mt-4">
                  <TabsList className="mb-4">
                    <TabsTrigger value="weather">–ü–æ–≥–æ–¥–∞</TabsTrigger>
                    <TabsTrigger value="recommendations">–†–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–∏</TabsTrigger>
                  </TabsList>

                  <TabsContent value="weather" className="mt-2 animate-fadeIn">
                    <div className="grid gap-4 sm:grid-cols-2 md:grid-cols-3">
                      {forecastData.map((day, index) => (
                        <WeatherCard
                          key={day.date || index}
                          weatherData={day}
                          date={day.date}
                          showDetails={false}
                        />
                      ))}
                    </div>
                  </TabsContent>

                  <TabsContent value="recommendations" className="mt-2 animate-fadeIn">
                    <div className="grid gap-4 sm:grid-cols-2 md:grid-cols-3">
                      {forecastData.map((day, index) => {
                        const forecatRec = forecastRecommendations[index];

                        if (forecatRec) {
                          return (
                            <Card key={day.date || index}>
                              <CardContent className="p-4">
                                <h3 className="font-medium text-lg mb-2 border-b pb-1">
                                  {new Date(day.date).toLocaleDateString("ru-RU", {
                                    weekday: "long",
                                    day: "numeric",
                                    month: "long",
                                  })}
                                </h3>

                                <div className="space-y-3 text-sm">
                                  {forecatRec.essential && forecatRec.essential.length > 0 && (
                                    <div>
                                      <h4 className="font-medium text-primary">
                                        –ù–µ–æ–±—Ö–æ–¥–∏–º—ã–µ –≤–µ—â–∏:
                                      </h4>
                                      <ul className="pl-2 mt-1 space-y-1">
                                        {forecatRec.essential.slice(0, 3).map((item: string, i: number) => (
                                          <li key={`essential-${i}`} className="flex items-center gap-2">
                                            <span className="w-1 h-1 rounded-full bg-primary inline-block"></span>
                                            {item}
                                          </li>
                                        ))}
                                      </ul>
                                    </div>
                                  )}

                                  {forecatRec.accessories && forecatRec.accessories.length > 0 && (
                                    <div>
                                      <h4 className="font-medium text-blue-500">–ê–∫—Å–µ—Å—Å—É–∞—Ä—ã:</h4>
                                      <ul className="pl-2 mt-1 space-y-1">
                                        {forecatRec.accessories.slice(0, 2).map((item: string, i: number) => (
                                          <li key={`acc-${i}`} className="flex items-center gap-2">
                                            <span className="w-1 h-1 rounded-full bg-blue-500 inline-block"></span>
                                            {item}
                                          </li>
                                        ))}
                                      </ul>
                                    </div>
                                  )}
                                </div>
                              </CardContent>
                            </Card>
                          );
                        }

                        // –ó–∞–ø–∞—Å–Ω–æ–π –≤–∞—Ä–∏–∞–Ω—Ç, –µ—Å–ª–∏ –Ω–µ—Ç —Ä–µ–∫–æ–º–µ–Ω–¥–∞—Ü–∏–π –¥–ª—è –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–≥–æ –¥–Ω—è
                        return (
                          <Card key={day.date || index}>
                            <CardContent className="p-4">
                              <h3 className="font-medium mb-2">
                                {new Date(day.date).toLocaleDateString("ru-RU", {
                                  weekday: "long",
                                  day: "numeric",
                                })}
                              </h3>
                              <ul className="text-sm space-y-1">
                                <li>
                                  ‚Ä¢ –í–µ—Ä—Ö–Ω—è—è –æ–¥–µ–∂–¥–∞:{" "}
                                  {day.temperature.air.C > 15
                                    ? "–ª–µ–≥–∫–∞—è –∫—É—Ä—Ç–∫–∞ –∏–ª–∏ –≤–µ—Ç—Ä–æ–≤–∫–∞"
                                    : "–∫—É—Ä—Ç–∫–∞"}
                                </li>
                                <li>
                                  ‚Ä¢ –û—Å–Ω–æ–≤–Ω–∞—è –æ–¥–µ–∂–¥–∞:{" "}
                                  {day.temperature.air.C > 20
                                    ? "–ª–µ–≥–∫–∞—è –æ–¥–µ–∂–¥–∞"
                                    : "–∫–æ–º—Ñ–æ—Ä—Ç–Ω–∞—è –æ–¥–µ–∂–¥–∞ —Å—Ä–µ–¥–Ω–µ–π –ø–ª–æ—Ç–Ω–æ—Å—Ç–∏"}
                                </li>
                                {day.precipitation?.type === "rain" && (
                                  <li className="text-blue-500">‚Ä¢ –ù–µ –∑–∞–±—É–¥—å—Ç–µ –∑–æ–Ω—Ç</li>
                                )}
                              </ul>
                            </CardContent>
                          </Card>
                        );
                      })}
                    </div>
                  </TabsContent>
                </Tabs>
              </div>
            </>
          )}
        </div>
      </main>
      <Footer />
    </div>
  );
}
